@font-face {
  font-family: "FiraGOBold";  
  src: local("FiraGOBold"),
    url("./fonts/FiraGO-Bold.otf") format("truetype");
}

@font-face {
  font-family: "FiraGOBook";  
  src: local("FiraGOBook"),
    url("./fonts/FiraGO-Book.otf") format("truetype");
}

.App{
  text-align: center;
}

.question-text {
  text-align: center;
  margin-top: 30px;
  display: block;
  color: #393D54;
  font-weight: bold;
  line-height: 1.4em;
  font-size: 1.08em;
  font-family: "FiraGOBold";
  padding-left: 12px;
  padding-right: 12px;
  max-width: 600px;
  margin: 0 auto;
  margin-top: 30px;
}

.question-section-wrapper{
  padding-top: 20px;
}

ul.answers-wrapper {
  width: 30%;
  margin: 0 auto;
  margin-top: 24px;
  padding-inline-start: 0px;
}

ul.answers-wrapper li{
  list-style: none;
  margin-bottom: 9px;
  background: #eceff1;
  border: 1px solid #d3d9df;
  padding: 5px 5px 8px;
  display: block;
  color: #393D54;
  position: relative;
  font-size: 16px;
  cursor: pointer;
  border-radius: 10px;
  box-shadow: 4px 4px 5px 0px rgba(0,0,0,0.44);
  -webkit-box-shadow: 4px 4px 5px 0px rgba(0,0,0,0.44);
  -moz-box-shadow: 4px 4px 5px 0px rgba(0,0,0,0.44);
  font-family: "FiraGOBook";

}
