.question-page-background{
  height: 100vh;
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.final-results-wrapper-bg{
  background-color: #70D9D9;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.final-results-wrapper{
  display: flex;
  flex-direction: column;
  height: 95vh;
  align-items: center;
  width:40%;
  margin: 0 auto;
  background-color: #FFFFFF;
  border-radius: 50px;
}

.score{
  background-color: #FF7F36;
  padding: 15px 35px;
  color: white;
  font-family: FiraGOBold;
  font-size: 22px;
  display: inline-block;
  border-radius: 20px;
  margin-top: 70px;
  font-feature-settings: "case";
}

.score-short-desc{
  color: #FF7F36;
  font-family: FiraGOBold;
  font-size: 22px;
  display: inline-block;
  margin-top: 23px;
  margin-bottom: 25px;
  margin-left: 15px;
  margin-right: 15px;
  font-feature-settings: "case";
}

.score-full-desc{
  font-family: FiraGOBook;
  font-size: 15px;
  color: #222433;
  margin-left: 25px;
  margin-right: 25px;
  text-align: start;
  line-height: 24px;
}

.score-page-bg{
  width: 100%;
  flex-grow: 1;
  background-position: top;
  background-repeat: no-repeat;
}

.result-1{
  background-image: url('imgs/result_bg1.svg');
}

.result-2{
  background-image: url('imgs/result_bg2.svg');
}

.result-3{
  background-image: url('imgs/result_bg3.svg');
}

.result-4{
  background-image: url('imgs/result_bg4.svg');
}



@media (max-width:780px) { /* big landscape tablets, laptops, and desktops */ 
  ul.answers-wrapper {
    width: 65%;
    margin-top: 20px;
  }

  ul.answers-wrapper li{
    list-style: none;
    margin-bottom: 9px;
    background: #eceff1;
    border: 1px solid #d3d9df;
    padding: 5px 5px 4px;
  }

  .final-results-wrapper{
    width:92%;
  }

  .score {
    margin-top: 30px;
    font-size: 18px;
    padding: 12px 30px;
    border-radius: 23px;
  }

  .score-short-desc{
    color: #FF7F36;
    font-family: FiraGOBold;
    font-size: 18px;
    display: inline-block;
    margin-top: 15px;
    margin-bottom: 20px;
  }

  .score-full-desc {
    font-size: 13px;
    line-height: 23px;
  }
 
}

